.loader-component{margin:auto;text-align:center;padding:25px 0px}.loader-component .square-spinner{width:40px;height:40px;background-color:#BF68E1;margin:20px auto;-webkit-animation:sk-rotateplane 1.2s infinite ease-in-out;animation:sk-rotateplane 1.2s infinite ease-in-out}.loader-component .circle-spinner{width:40px;height:40px;position:relative;margin:0px auto}.loader-component .double-bounce1,.loader-component .double-bounce2{width:100%;height:100%;border-radius:50%;background-color:rgba(191,104,225,0.5);opacity:0.6;position:absolute;top:0;left:0;-webkit-animation:sk-bounce 1.5s infinite ease-in-out;animation:sk-bounce 1.5s infinite ease-in-out}.loader-component .double-bounce2{-webkit-animation-delay:-1.0s;animation-delay:-1.0s}@-webkit-keyframes sk-rotateplane{0%{-webkit-transform:perspective(120px)}50%{-webkit-transform:perspective(120px) rotateY(180deg)}100%{-webkit-transform:perspective(120px) rotateY(180deg) rotateX(180deg)}}@keyframes sk-rotateplane{0%{transform:perspective(120px) rotateX(0deg) rotateY(0deg);-webkit-transform:perspective(120px) rotateX(0deg) rotateY(0deg);opacity:1}50%{transform:perspective(120px) rotateX(-180.1deg) rotateY(0deg);-webkit-transform:perspective(120px) rotateX(-180.1deg) rotateY(0deg);opacity:1}100%{transform:perspective(120px) rotateX(-180deg) rotateY(-179.9deg);-webkit-transform:perspective(120px) rotateX(-180deg) rotateY(-179.9deg);opacity:1}}@-webkit-keyframes sk-bounce{0%,100%{-webkit-transform:scale(0)}50%{-webkit-transform:scale(1)}}@keyframes sk-bounce{0%,100%{transform:scale(0);-webkit-transform:scale(0)}50%{transform:scale(1);-webkit-transform:scale(1)}}body{padding-top:10px}



